import api from '@/common/api.js'
import { Config } from '@/config/config.js'
export default {
	data() {
		return {
			uname: '',
			phone: '',
			pwd: '',
			pwd2: '',
			email: ''
		}
	},
	methods: {
		onNameInput(event) {
			this.uname = event.target.value;
		},
		onPhoneInput(event) {
			this.phone = event.target.value;
		},
		onPwdInput(event) {
			this.pwd = event.target.value;
		},
		onConfirmPwdInput(event) {
			this.pwd2 = event.target.value;
		},
		onEmailInput(event) {
			this.email = event.target.value;
		},
		check() {
			var reg = /^1\d{10}$/;                          // 验证手机
			var reg2= /^(?![^a-zA-Z]+$)(?!\D+$)/;           // 验证密码
			var reg3 = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;   // 验证邮箱
			/*if (this.uname == '') {
				this.showToast('请输入昵称');
				return false;
			} else */if (this.phone == '') {
				this.showToast('请输入手机号');
				return false;
			} else if (!reg.test(this.phone)) {
				this.showToast('手机号格式错误');
				return false;
			} else if (this.pwd == '') {
				this.showToast('请输入密码');
				return false;
			}  else if (!this.isValidLength(this.pwd)) {
				this.showToast('请输入6~24位密码');
				return false;
			} else if (!reg2.test(this.pwd)) {
				this.showToast('密码必须包含数字和字母');
				return false;
			} else if (this.pwd2 == '') {
				this.showToast('请输入确认密码');
				return false;
			} else if (this.pwd != this.pwd2) {
				this.showToast('两次输入的密码不一致');
				return false;
			} else if (this.email != '' && !reg3.test(this.email)) {
				this.showToast('请输入合法的电子邮箱');
				return false;
			}
			return true;
		},		
		isValidLength(str) {
			if (str.length < 6 || str.length.length > 24) return false;
			else return true;
		},
		regist() {
			let params = {
					nickName: this.uname,
					phone: this.phone,
					password: this.pwd,
					email: this.email
			};
			api.regist(params).then((result)=>{
				if (result.code == 0) {
					getApp().globalData.token = result.data.token;
					try {
					    uni.setStorageSync('token', result.data.token);							
					} catch (e) {
					    // error
					}  
					let pages = getCurrentPages();
					let prevPage = pages[pages.length - 3];
					prevPage.onLoad();
					uni.showToast({
						title: result.msg,
						icon: "none",
						success: () => {
							setTimeout(()=>{
								uni.navigateBack();		
							}, 1500);								
						}
					});						
				} else {
					this.showToast(result.msg);					
				}
				// uni.switchTab({
				// 	url: '/pages/account/account',
				// 	success(e) {
				// 		var page = getCurrentPages().pop();
				// 		if (page == undefined || page == null) return;
				// 		page.onLoad();
				// 	}
				// })
			});
		},
		goService() {
			uni.navigateTo({
				url: '/pages/account/service'
			});
		},
		showToast(msg) {
			uni.showToast({
				title: msg,
				icon: 'none'
			})
		}
	}
}
